        font-size: 20px;
        border: 1px solid;
        border-radius: 15px;
        padding: 5px 10px;
        text-align: center;
      ">
      捷德 NFT 数字藏品演示程序
    </div>
    <br />
    <br />
    <v-text-field autofocus prepend-inner-icon="mdi-onepassword" v-model="password" color="#9C27B0" label="请输入演示密码..." outlined clearable></v-text-field>
    <br />
    <v-btn x-large class="white--text" block color="#9C27B0" @click="nextStep">
